<template>
  <div style="background-color: #EBEBEB;min-height:900px">
    <div >
      <el-row >
        <el-col >
          <div>
					 <el-menu
								default-active="1"
								class="el-menu-vertical-demo" style="min-height:900px" background-color="#333744" text-color="#fff"  active-text-color="#ffd04b">
                 <router-link to="/index">
							    <el-menu-item index="1">
                 	<i class="fa fa-home"></i>
										<span slot="title">首页</span>
                  </el-menu-item> 
                  </router-link>
									<el-submenu index="2">
									<template slot="title">
										<i class="fa fa-chain"></i>
										<span>账户开通</span>
									</template>
									<el-menu-item-group>
                    <router-link to="/form">
                      	<el-menu-item index="2-1">代理商开户</el-menu-item>
                    </router-link>
									  <el-menu-item index="3-2">业务员开户</el-menu-item>
									  <el-menu-item index="3-2">企业开户</el-menu-item>
									</el-menu-item-group>
								</el-submenu>
							
								<el-menu-item index="3" >
									<i class="fa fa-wifi"></i>
									<span slot="title">导航三</span>
								</el-menu-item>
								<el-menu-item index="4">
									<i class="fa fa-tasks"></i>
									<span slot="title">导航四</span>
								</el-menu-item>
							</el-menu>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    data(){
      return {
        searchCriteria: '',
        breadcrumbItems: ['导航一'],
      }
    },

    methods:{
      handleIconClick(ev) {
        console.log(ev);
      },

      handleSelect(key, keyPath){
        switch(key){
          case '1':
            this.$router.push('/Page1');
            this.breadcrumbItems  = ['导航一']
            break;
          case '2':
            this.$router.push('/Page2')
            this.breadcrumbItems  = ['导航二']
            break;
          case '3':
            this.$router.push('/Page3')
            this.breadcrumbItems  = ['导航三']
            break;
        }
      },

    },
  }
</script>


